











































































import Csv from 'view-design/src/utils/csv'
import ExportCsv from 'view-design/src/components/table/export-csv'
import editor from "@/components/editor.vue";
import {Component, Mixins, Prop} from "vue-property-decorator";
import att_mixins from "@/mixins/basic";
// eslint-disable-next-line no-unused-vars
import {CreateElement} from "vue";
import module_general from "@/store/modules/general";

const export_csv = function exportCsv(this: any, params: any) {
    if (params.filename) {
        if (params.filename.indexOf('.csv') === -1) {
            params.filename += '.csv'
        }
    } else {
        params.filename = 'table.csv'
    }

    let columns = []
    let datas = []
    if (params.columns && params.data) {
        columns = params.columns
        datas = params.data
    } else {
        columns = this.columns
        if (!('original' in params)) params.original = true
        datas = params.original ? this.data : this.rebuildData
    }

    let noHeader = false
    if ('noHeader' in params) noHeader = params.noHeader
    const data = Csv(columns, datas, params, noHeader)
    if (params.callback) {
        params.callback(data)
    } else {
        ExportCsv.download(params.filename, data)
    }
}
@Component({components: {editor}})
export default class tabQuery extends Mixins(att_mixins) {

    @Prop({
        required: false,
        default: false,
        type: Boolean
    }) export_data !: boolean

    @Prop({
        required: false,
        default: '',
        type: String
    }) table !: string

    @Prop({
        required: false,
        default: '',
        type: String
    }) dataBase !: string

    @Prop({
        required: false,
        default: '',
        type: String
    }) source !: string


    get snippetList() {
        return module_general.snippet
    }

    openDrawer = false
    expireInfo = false
    page_size = 10
    columnsName: object[] = []
    queryRes = []
    allQueryData = []
    total = 0
    private sqls = ''
    fieldColumns = [
        {
            title: '字段名',
            key: 'field'
        },
        {
            title: '字段类型',
            key: 'type',
            editable: true
        },
        {
            title: '字段是否为空',
            key: 'null',
            editable: true,
            option: true
        },
        {
            title: '默认值',
            key: 'default',
            editable: true
        },
        {
            title: '索引类型',
            key: 'key',
            editable: true
        },
        {
            title: '备注',
            key: 'comment'
        }
    ]
    queryTime = ''

    delSnippet(vl: any) {
        module_general.snippetRemoveTag(vl)
    }

    copySnippet(vl: { text: any; }) {
        this.sqls = vl.text
    }

    openSnippet() {
        this.openDrawer = true
    }

    fetchTableField() {
        if (this.dataBase === '' || this.table === '') {
            this.$Message.error("请选中对应库/表");
            return
        }
        this.$http.get(`${this.$config.url}/query/table_info?base=${this.dataBase}&table=${this.table}&source=${this.source}`)
            .then((res: { data: never[]; }) => {
                this.columnsName = this.fieldColumns;
                this.queryRes = res.data
                this.$Message.success({content: "已获取表结构!"})
            })
            .catch((err: any) => {
                this.$config.err_notice(this, err)
            })

    }

    exportdata() {
        export_csv({
            filename: 'Yearning_Data',
            original: true,
            data: this.allQueryData,
            columns: this.columnsName
        })
    }

    splice_arr(page: number) {
        this.queryRes = this.allQueryData.slice(page * this.page_size - this.page_size, page * this.page_size)
    }

    ex_arr(n: number) {
        this.page_size = n;
        this.queryRes = this.allQueryData.slice(this.current * n - n, this.current * n)
    }

    clearObj() {
        this.sqls = ''
        this.queryRes = [];
        this.columnsName = [];
        this.current = 1;
        this.total = 0
    }

    togo() {
        this.$router.push({
            name: 'query'
        });
    }

    querySQL() {
        this.columnsName = [];
        this.queryRes = [];
        let spin: any = this.$Spin
        spin.show({
            render: (h: CreateElement) => {
                return h('div', [
                    h('Icon', {
                        'class': 'demo-spin-icon-load',
                        props: {
                            type: 'ios-loading',
                            size: 18
                        }
                    }),
                    h('div', 'Loading')
                ])
            }
        });
        this.$http.post(`${this.$config.url}/query/results`, {
            'sql': this.sqls,
            'basename': this.dataBase,
            'source': this.source
        })
            .then((res: { data: any }) => {
                if (res.data.status) {
                    this.expireInfo = true;
                    return
                }
                if (res.data.data === null) {
                    this.columnsName = res.data.title;
                    this.allQueryData = [];
                } else if (!res.data.data) {
                    this.$Message.error({
                        content: res.data,
                        duration: 4,
                        top: 50
                    })
                } else {
                    this.queryTime = res.data.time;
                    this.columnsName = res.data.title;
                    this.allQueryData = res.data.data;
                    this.queryRes = this.allQueryData.slice(0, this.page_size);
                    this.total = res.data.data.length
                }

            })
            .catch((err: any) => {
                this.$config.err_notice(this, err);
            })
            .finally(spin.hide())

    }
}
